.App {
  text-align: center;
  font-family: Arial;
}

.selected {
  background: rgb(255, 0, 191) !important;
  border-radius: 8px;
  height: 50px;
  border: 3px solid rgb(0, 0, 0);
  color: rgb(70, 77, 180);
  font-weight: bolder;
  padding: 5px;
}

.options {
  padding: 8px;
  border: 3px solid rgb(0, 0, 0);
  cursor: pointer;
  width: 100%;
  background-color: skyblue;
  color: steelblue;
  font-weight: bold;
  border-radius: 7px;
}
.optionsbtn {
  padding: 8px;
  border: 3px solid rgb(0, 0, 0);
  cursor: pointer;
  width: 35%;
  background-color: rgb(138, 235, 135);
  color: steelblue;
  font-weight: bold;
  border-radius: 7px;
}
ul {
  list-style-type: none;
}

ul li {
  margin: 5px;
}

body {
  display: flex;
  justify-content: center;
  color: white;
  font-family: "Segoe UI";
  font-size: 18px;
  background-color: teal;
}
